a {
  display: inline-block;
}

.p-12px {
  padding: 12px;
}

.py-12px {
  padding-top: 12px;
  padding-bottom: 12px;
}

.p-32px {
  padding: 32px;
}

.px-32px {
  padding-left: 32px;
  padding-right: 32px;
}

.h-250px {
  height: 250px !important;
}

.ms-9px {
  margin-left: 9px;
}

.ms-50px {
  margin-left: 50px;
}

.fs-12px {
  font-size: 12px;
}

.fs-14px {
  font-size: 14px;
}

code {
  color: #198754;
  background-color: #f8f9fa;
}

textarea {
  resize: none;
}

.active-link {
  position: relative;
}

.active-link::before {
  content: '';
  background-color: #198754;
  position: absolute;
  top: 0;
  bottom: 0;
  left: -32px;
  width: 4px;
}

.profile-picture {
  object-fit: cover;
  height: 40px;
  width: 40px;
  border-radius: 50%;
}

.material-icons {
  transition: all 100ms ease-in-out;
}

.material-icons:hover {
  color: #1cb0ff;
  cursor: pointer;
}

.table-head-icon {
  font-size: 12px;
}

.view-modal-picture {
  width: 141px;
  height: 141px;
  object-fit: cover;
}

.profile-view-more.collapsed .expand-more {
  transform: rotate(0deg);
  transition: all 1s ease-in-out;
}

.profile-view-more .expand-more {
  transform: rotate(180deg);
}
